* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
button {
    cursor: pointer;
}
nav {
    height: 8vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}
.toggle {
    border: 0;
    height: 30px;
    width: 100px;
}
.filter-message-container {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10%;
    z-index: -2;
}
.message {
    width: 40%;
    min-width: 150px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
    padding: 0 10px;
}
.filter {
    width: 40%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    overflow: hidden;
}
.holder {
    width: 100%;
    max-width: 200px;
    height: 40px;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 0 0 15px 1px rgba(0,0,0,0.1);
    background-color: red;
    z-index: 2;
    cursor: pointer;
}
.holder * {
    pointer-events: none;
}
.container {
    width: 100%;
    max-width: 200px;
    min-height: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    transform: translateY(-100%);
    transition: all ease .5s;
}
.container p {
    cursor: pointer;
}
.slide-from-top {
    transform: translateY(0);
}
.todo-list {
    width: 80%;
    height: 50vh;
    margin: 20px auto;
    padding: 10px 0;
    overflow-y: scroll;
}
.no {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
    pointer-events: none;
}
.todo {
    width: 100%;
    height: 50px;
    margin-bottom: 10px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.todo p {
    width: 60%;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
}
.btn {
    width: 30%;
    max-width: 100px;
    display: flex;
    justify-content: space-between;
}
.btn button {
    width: 40px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.btn button i {
    pointer-events: none;
}
.hide {
    display: none;
}
form {
    width: 100%;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
}
form input {
    width: 60%;
    height: 40px;
    border: 0;
    padding: 0 20px;
    outline: none;
}
form button {
    border: 0;
    height: 35px;
    width: 100px;
}

.completed {
    text-decoration: line-through;
}